.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

@font-face {
  font-family: myunique1;
  src: url(../public/BadGuyBlack.ttf);
}

@font-face {
  font-family: myunique2;
  src: url(../public/KGAllofMe.ttf);
}

body {
  position: relative;
  padding-top: 50px;
  /* background-color: bisque; */
}

header {
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 40 auto;
  margin-top: 30px;
  background-color: white;
  text-align: center;
  font-size: 24px;
  color: black;
  font-weight: bold;
}

h1 {
  text-shadow: 4px 4px 5px gray;
  font-family: myunique1, Arial, Helvetica, sans-serif;
}

h1:hover {
  transform: scale(110%);
}

img {
  width: 180px;
}

img:hover {
  transform: scale(110%);
  filter: drop-shadow(5px 5px 5px black);
}

nav {
  color: white;
  background-color: blue;
  border-bottom: 2px solid black;
  text-align: center;
  font-size: 24px;
  padding: 15px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

nav ul {
  display: flex;
  justify-content: space-around;
}

nav ul li {
  display: inline;
  padding: 5px;
}

.nav-link.active {
  color: black;
  text-shadow: 0 0 10px white, 0 0 20px white, 0 0 42px white, 0 0 82px white,
    0 0 92px white;
}

nav a {
  font-family: myunique2, Arial, Helvetica, sans-serif;
  color: white;
  text-decoration: none;
}

a:hover {
  color: black;
  text-shadow: 0 0 10px white, 0 0 20px white, 0 0 42px white, 0 0 82px white,
    0 0 92px white;
}

main {
  text-align: center;
}

main p {
  text-align: left;
  text-indent: 5%;
  line-height: 26px;
}

section {
  display: block;
  padding: 25px;
}

h2 {
  font-family: myunique2, Arial, Helvetica, sans-serif;
  font-size: 32px;
  color: black;
  font-weight: bold;
  padding-bottom: 20px;
}

h2:hover {
  transform: scale(110%);
  filter: drop-shadow(5px 5px 5px black);
}

.about {
  border: 5px solid black;
  box-shadow: 5px 5px 5px;
  margin: 10px 10px 20px 10px;
  background: white;
}

/* this allows the link target to be 50px down from the top so it's not hidden by the fixed nav bar */
#about,
#resume,
#portfolio {
  scroll-margin-top: 50px;
  margin-left: 20px;
  margin-right: 20px;
}

#about p {
  text-align: left;
  text-indent: 25px;
  line-height: 1.5;
}

.resume {
  border: 5px solid black;
  box-shadow: 5px 5px 5px;
  margin: 10px 10px 20px 10px;
  background: white;
  line-height: 1.5;
}

.resume ul {
  list-style: none;
  text-align: left;
  text-indent: 5%;
  margin-bottom: 50px;
}

#resTitle {
  font-weight: bold;
}

.portfolio {
  border: 5px solid black;
  box-shadow: 5px 5px 5px;
  margin: 10px 10px 20px 10px;
  background: white;
}

.portfolioimages {
  display: flex;
  flex-direction: row;
}

#projects {
  height: auto;
  flex: 10%;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: center;
}

#projects figure img {
  width: 200px;
  margin: 60px;
  max-width: 33vw;
  border: 1px solid black;
}

#projects figure img:hover {
  cursor: pointer;
}

figure {
  position: relative;
  display: flex;
  flex-direction: column;
}

figcaption {
  font-family: myunique2, Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: bold;
}

.projButtons {
  position: absolute;
  top: 10%;
  align-self: center;
}

.gitButton,
.depButton {
  font-size: 12px;
  text-align: center;
  margin: 10px;
  align-self: center;
}

.gitButton:hover,
.depButton:hover {
  margin: 5px;
  cursor: pointer;
}

.contact-submission {
  margin: auto;
  margin-top: 40px;
  margin-bottom: 50px;
  border-radius: 25px;
  border: 5px solid black;
  box-shadow: 5px 5px 5px;
  width: 70%;
  height: fit-content;
  padding: 10px 20px;
  font-weight: bold;
}

#name,
#email {
  height: 30px;
  position: relative;
  width: 80%;
  padding: 10px;
  margin: 5px;
  font-weight: bold;
}

#message {
  height: 100px;
  position: relative;
  width: 80%;
  padding: 10px;
  margin: 5px;
  font-weight: bold;
}

button,
#respdf {
  color: white;
  background-color: red;
  border-radius: 10% / 50%;
  font-size: 20px;
  padding: 5px;
  padding-left: 20px;
  padding-right: 20px;
}

button:hover,
#respdf:hover {
  transform: scale(110%);
  filter: drop-shadow(5px 5px 5px black);
  cursor: pointer;
  text-shadow: none;
}

#respdf {
  text-decoration: none;
  border: 2px solid black;
}

footer {
  text-align: center;
  color: white;
  background-color: blue;
  padding: 5px;
}

footer a {
  text-decoration: none;
  color: lightgray;
  margin: 20px;
  padding: 5px;
}

#respdf {
  border-radius: 0;
  margin-top: 25px;
}

#respdf:hover {
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  nav ul li a {
    font-size: 50%;
  }
  h1 {
    font-size: 90%;
  }
}

@media screen and (min-width: 1000px) {
  #about,
  #resume,
  #portfolio {
    margin-left: 40px;
    margin-right: 40px;
  }
}

@media screen and (min-width: 1500px) {
  #about,
  #resume,
  #portfolio {
    margin-left: 60px;
    margin-right: 60px;
  }
}
